<template>
  <div class="welcome-container">
    <div class="animation-wrapper">
      <div class="text-container">
        <h1>
          {{ displayedText }}
          <span class="cursor"></span>
        </h1>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'WelcomeAnimation',
  data() {
    return {
      welcomeTexts: [
        'æ¬¢è¿æ¥åˆ° ç§‘æŠ€å¼•é¢†æ—¶ä»£ æ¢ç´¢æ— é™å¯èƒ½ è¿™é‡Œæ˜¯å±•ç¤ºåˆ›æ„é±¼æŠ€æœ¯çš„å¹³å°ã€‚',
        'æ·±å¤œä¸‹ç­ çœ‹çœ‹ä»Šå¤©çš„ä¸–ç•Œåˆå‘ç”Ÿäº†ä»€ä¹ˆå§ï¼Ÿï¼',
        'äººæ€»è¦è¿›æ­¥ ç§‘æŠ€ä¹Ÿæ˜¯',
        'æ‡’ ä¹Ÿæ˜¯åœ¨è¿›æ­¥ ç§‘æŠ€ ä¹Ÿæ˜¯æ‡’çš„ä¸€éƒ¨åˆ†',
        'åˆ›æ–°æ”¹å˜ä¸–ç•Œ ç§‘æŠ€ç‚¹äº®æœªæ¥',
        'æ¯ä¸€å¤©éƒ½æ˜¯æ–°çš„å¼€å§‹ æ¯ä¸€æ¬¡æ€è€ƒéƒ½æ˜¯è¿›æ­¥',
        'æ¢ç´¢æœªçŸ¥ åˆ›é€ å¯èƒ½'
      ],
      displayedText: '',
      currentTextIndex: 0,
      currentCharIndex: 0,
      isDeleting: false,
      // æ–°çš„æ‰“å­—æœºå‚æ•°è®¾ç½®
      typeSpeed: 80,      // æ‰“å­—é€Ÿåº¦ï¼ˆåŠ å¿«ï¼‰
      deleteSpeed: 100,    // åˆ é™¤é€Ÿåº¦
      pauseAfterType: 2000,// æ‰“å­—å®Œæˆåæš‚åœæ—¶é—´ï¼ˆç¼©çŸ­ï¼‰
      pauseAfterDelete: 500,// åˆ é™¤å®Œæˆåæš‚åœæ—¶é—´
      // åŠ¨ç”»æ§åˆ¶å˜é‡
      animationId: null,
      lastTime: 0,
      // éšæœºåŒ–æ‰“å­—é€Ÿåº¦ï¼Œå¢åŠ çœŸå®æ„Ÿ
      randomSpeed: true,
      minSpeedVariation: 0.7,  // æœ€å°é€Ÿåº¦ç³»æ•°
      maxSpeedVariation: 1.3   // æœ€å¤§é€Ÿåº¦ç³»æ•°
    }
  },
  mounted() {
    // å»¶è¿Ÿå¯åŠ¨ï¼Œè®©é¡µé¢å®Œå…¨åŠ è½½
    setTimeout(() => {
      this.typeWriterLoop()
    }, 300)
  },
  beforeUnmount() {
    // æ¸…ç†åŠ¨ç”»å¸§
    if (this.animationId) {
      cancelAnimationFrame(this.animationId)
    }
  },
  methods: {
    // ä½¿ç”¨requestAnimationFrameå®ç°çš„æ‰“å­—æœºåŠ¨ç”»å¾ªç¯
    typeWriterLoop(timestamp = 0) {
      if (!this.lastTime) {
        this.lastTime = timestamp
      }
      
      const currentText = this.welcomeTexts[this.currentTextIndex]
      const speedFactor = this.randomSpeed ? 
        (Math.random() * (this.maxSpeedVariation - this.minSpeedVariation) + this.minSpeedVariation) : 1
      
      // è®¡ç®—å½“å‰åº”è¯¥ä½¿ç”¨çš„æ—¶é—´é—´éš”
      const currentSpeed = this.isDeleting ? 
        (this.deleteSpeed / speedFactor) : 
        (this.typeSpeed / speedFactor)
      
      // æ£€æŸ¥æ˜¯å¦åº”è¯¥æ‰§è¡Œä¸‹ä¸€æ­¥æ“ä½œ
      if (timestamp - this.lastTime > currentSpeed) {
        if (this.isDeleting) {
          // åˆ é™¤æ¨¡å¼
          this.displayedText = currentText.substring(0, this.currentCharIndex - 1)
          this.currentCharIndex--
          
          if (this.currentCharIndex === 0) {
            // åˆ é™¤å®Œæˆï¼Œåˆ‡æ¢åˆ°ä¸‹ä¸€æ®µæ–‡æœ¬
            this.isDeleting = false
            this.currentTextIndex = (this.currentTextIndex + 1) % this.welcomeTexts.length
            
            // æš‚åœä¸€æ®µæ—¶é—´åå†å¼€å§‹ä¸‹ä¸€æ®µæ–‡æœ¬
            setTimeout(() => {
              this.lastTime = 0
              this.animationId = requestAnimationFrame(t => this.typeWriterLoop(t))
            }, this.pauseAfterDelete)
            return
          }
        } else {
          // æ‰“å­—æ¨¡å¼
          this.displayedText = currentText.substring(0, this.currentCharIndex + 1)
          this.currentCharIndex++
          
          if (this.currentCharIndex === currentText.length) {
            // æ‰“å­—å®Œæˆï¼Œå‡†å¤‡åˆ é™¤
            this.isDeleting = true
            
            // æš‚åœä¸€æ®µæ—¶é—´åå¼€å§‹åˆ é™¤
            setTimeout(() => {
              this.lastTime = 0
              this.animationId = requestAnimationFrame(t => this.typeWriterLoop(t))
            }, this.pauseAfterType)
            return
          }
        }
        
        this.lastTime = timestamp
      }
      
      // ç»§ç»­åŠ¨ç”»å¾ªç¯
      this.animationId = requestAnimationFrame(t => this.typeWriterLoop(t))
    },
    
    // æ·»åŠ ä¸€ä¸ªæ–¹æ³•æ¥é‡ç½®æ‰“å­—æœºæ•ˆæœï¼ˆå¦‚æœéœ€è¦ï¼‰
    resetTypewriter() {
      if (this.animationId) {
        cancelAnimationFrame(this.animationId)
      }
      this.displayedText = ''
      this.currentTextIndex = 0
      this.currentCharIndex = 0
      this.isDeleting = false
      this.lastTime = 0
      this.animationId = requestAnimationFrame(t => this.typeWriterLoop(t))
    }
  }
}
</script>

