// å»¶è¿Ÿå‡½æ•°ï¼Œæ¨¡æ‹Ÿç½‘ç»œè¯·æ±‚
const delay = (ms) => new Promise(resolve => setTimeout(resolve, ms));

/**
 * è·å–æ‰€æœ‰æ ‡ç­¾
 * @returns {Promise} æ ‡ç­¾åˆ—è¡¨
 */
export const getAllTags = async () => {
  if (USE_MOCK_DATA) {
    await delay(300);
    return { data: mockTags };
  }
  return api.get('/tags');
};

/**
 * è·å–çƒ­é—¨æ ‡ç­¾
 * @param {number} limit é™åˆ¶æ•°é‡
 * @returns {Promise} çƒ­é—¨æ ‡ç­¾åˆ—è¡¨
 */
export const getPopularTags = async (limit = 10) => {
  if (USE_MOCK_DATA) {
    await delay(200);
    return { 
      data: [...mockTags]
        .sort((a, b) => b.count - a.count)
        .slice(0, limit)
    };
  }
  return api.get(`/tags/popular?limit=${limit}`);
};

/**
 * è·å–åšå®¢çš„æ ‡ç­¾
 * @param {number} blogId åšå®¢ID
 * @returns {Promise} åšå®¢çš„æ ‡ç­¾åˆ—è¡¨
 */
export const getBlogTags = async (blogId) => {
  if (USE_MOCK_DATA) {
    await delay(200);
    const blogTag = mockBlogTags.find(bt => bt.blogId === blogId);
    if (blogTag) {
      const tags = mockTags.filter(tag => blogTag.tagIds.includes(tag.id));
      return { data: tags };
    }
    return { data: [] };
  }
  return api.get(`/tags/blog/${blogId}`);
};

/**
 * æ ¹æ®æ ‡ç­¾è·å–åšå®¢
 * @param {number} tagId æ ‡ç­¾ID
 * @param {string} sortBy æ’åºæ–¹å¼ï¼šlatest(æœ€æ–°)ã€hot(çƒ­é—¨)ã€likes(ç‚¹èµæ•°)
 * @returns {Promise} åšå®¢åˆ—è¡¨
 */
export const getBlogsByTag = async (tagId, sortBy = 'latest') => {
  if (USE_MOCK_DATA) {
    await delay(400);
    
    // è·å–æ‰€æœ‰åšå®¢æ•°æ®
    const blogsResponse = await getBlogList();
    const allBlogs = blogsResponse.data || [];
    
    // è·å–åŒ…å«è¯¥æ ‡ç­¾çš„åšå®¢IDåˆ—è¡¨
    const blogIds = mockBlogTags
      .filter(bt => bt.tagIds.includes(tagId))
      .map(bt => bt.blogId);
    
    // è·å–å®Œæ•´çš„åšå®¢æ•°æ®
    const blogs = allBlogs.filter(blog => blogIds.includes(blog.id));
    
    // æ ¹æ®æ’åºå‚æ•°è¿›è¡Œæ’åº
    switch (sortBy) {
      case 'hot':
        // æŒ‰æµè§ˆé‡æ’åº
        blogs.sort((a, b) => b.viewCount - a.viewCount);
        break;
      case 'likes':
        // æŒ‰ç‚¹èµæ•°æ’åº
        blogs.sort((a, b) => b.likeCount - a.likeCount);
        break;
      case 'latest':
      default:
        // æŒ‰åˆ›å»ºæ—¶é—´å€’åºæ’åº
        blogs.sort((a, b) => new Date(b.createdAt) - new Date(a.createdAt));
        break;
    }
    
    return { data: blogs };
  }
  return api.get(`/tags/${tagId}/blogs?sortBy=${sortBy}`);
};
