<template>
  <div class="home">
    <header class="page-header">
        <!-- æ¬¢è¿åŠ¨ç”»ç»„ä»¶ -->
        <div class="welcome-section">
          <WelcomeAnimation />
        </div>
      </header>
    
    <div class="main-container">
      <!-- å·¦ä¾§ï¼šåšå®¢åˆ—è¡¨ -->
      <div class="blog-section">
        <!-- æœç´¢æ¡† -->
        <div class="search-container">
          <input 
            type="text" 
            v-model="searchKeyword" 
            placeholder="æœç´¢åšå®¢æ ‡é¢˜æˆ–å†…å®¹..."
            class="search-input"
          />
          <button class="btn search-btn" @click="handleSearch">
            <svg width="18" height="18" viewBox="0 0 24 24" fill="currentColor">
              <path d="M15.5 14h-.79l-.28-.27A6.471 6.471 0 0 0 16 9.5 6.5 6.5 0 1 0 9.5 16c1.61 0 3.09-.59 4.23-1.57l.27.28v.79l5 4.99L20.49 19l-4.99-5zm-6 0C7.01 14 5 11.99 5 9.5S7.01 5 9.5 5 14 7.01 14 9.5 11.99 14 9.5 14z"/>
            </svg>
            æœç´¢
          </button>
          <button v-if="searchKeyword" class="btn reset-btn" @click="resetSearch">
            <svg width="18" height="18" viewBox="0 0 24 24" fill="currentColor">
              <path d="M19 6.41L17.59 5 12 10.59 6.41 5 5 6.41 10.59 12 5 17.59 6.41 19 12 13.41 17.59 19 19 17.59 13.41 12z"/>
            </svg>
            é‡ç½®
          </button>
        </div>
        
        <!-- æ’åºé€‰æ‹©å™¨ -->
        <div class="sort-container">
          <label class="sort-label">æ’åºæ–¹å¼:</label>
          <div class="sort-options">
            <button 
              class="sort-option" 
              :class="{ active: sortBy === 'latest' }"
              @click="setSortBy('latest')"
            >
              æœ€æ–°
            </button>
            <button 
              class="sort-option" 
              :class="{ active: sortBy === 'hot' }"
              @click="setSortBy('hot')"
            >
              çƒ­åº¦
            </button>
            <button 
              class="sort-option" 
              :class="{ active: sortBy === 'likes' }"
              @click="setSortBy('likes')"
            >
              ç‚¹èµé‡
            </button>
          </div>
        </div>
        
        <div class="blog-list">
          <div v-for="blog in displayBlogs" :key="blog.id" class="blog-card">
            <h2 class="blog-title">{{ blog.title }}</h2>
            <p class="blog-summary">{{ getSummary(blog.content) }}</p>
            
            <!-- åšå®¢ç»Ÿè®¡æ•°æ® -->
            <div class="blog-stats">
              <span class="stat-item views">
                <svg width="16" height="16" viewBox="0 0 24 24" fill="currentColor">
                  <path d="M12 4.5C7 4.5 2.73 7.61 1 12c1.73 4.39 6 7.5 11 7.5s9.27-3.11 11-7.5c-1.73-4.39-6-7.5-11-7.5zM12 17c-2.76 0-5-2.24-5-5s2.24-5 5-5 5 2.24 5 5-2.24 5-5 5zm0-8c-1.66 0-3 1.34-3 3s1.34 3 3 3 3-1.34 3-3-1.34-3-3-3z"/>
                </svg>
                <span>{{ formatNumber(blog.viewCount || 0) }}</span>
              </span>
              <span class="stat-item likes">
                <svg width="16" height="16" viewBox="0 0 24 24" fill="currentColor">
                  <path d="M12 21.35l-1.45-1.32C5.4 15.36 2 12.28 2 8.5 2 5.42 4.42 3 7.5 3c1.74 0 3.41.81 4.5 2.09C13.09 3.81 14.76 3 16.5 3 19.58 3 22 5.42 22 8.5c0 3.78-3.4 6.86-8.55 11.54L12 21.35z"/>
                </svg>
                <span>{{ formatNumber(blog.likeCount || 0) }}</span>
              </span>
              <span class="stat-item comments">
                <svg width="16" height="16" viewBox="0 0 24 24" fill="currentColor">
                  <path d="M20 2H4c-1.1 0-2 .9-2 2v12c0 1.1.9 2 2 2h14l4 4V4c0-1.1-.9-2-2-2zm-2 12H6v-2h12v2zm0-3H6V9h12v2zm0-3H6V6h12v2z"/>
                </svg>
                <span>{{ formatNumber(blog.commentCount || 0) }}</span>
              </span>
              <span class="stat-item forwards">
                <svg width="16" height="16" viewBox="0 0 24 24" fill="currentColor">
                  <path d="M12 8l-6 6 1.41 1.41L12 10.83l4.59 4.58L18 14l-6-6z"/>
                </svg>
                <span>{{ formatNumber(blog.forwardCount || 0) }}</span>
              </span>
            </div>
            
            <!-- æ˜¾ç¤ºåšå®¢æ ‡ç­¾ -->
            <div class="blog-tags" v-if="blog.tags && blog.tags.length > 0">
              <span class="tag" v-for="tag in blog.tags" :key="tag.id || tag">
                # {{ tag.name || tag }}
              </span>
            </div>
            
            <div class="blog-info">
              <span class="author">ä½œè€…: {{ blog.author }}</span>
              <button 
                class="follow-btn"
                :class="{ 'followed': isFollowing(blog.author) }"
                @click="toggleFollow(blog.author, $event)"
              >
                {{ isFollowing(blog.author) ? 'å·²å…³æ³¨' : 'å…³æ³¨' }}
              </button>
              <span class="status">{{ blog.status === 'published' ? 'å·²å‘å¸ƒ' : 'è‰ç¨¿' }}</span>
            </div>
            <button class="btn view-btn" @click="viewDetail(blog.id)"><span>æŸ¥çœ‹è¯¦æƒ…</span></button>
          </div>
          <div v-if="displayBlogs.length === 0 && !loading" class="empty">
            <div class="empty-icon">ğŸ“</div>
            <p>{{ searchKeyword ? 'æ²¡æœ‰æ‰¾åˆ°ç›¸å…³åšå®¢' : 'æš‚æ— åšå®¢æ–‡ç« ' }}</p>
          </div>
          <div v-if="loading" class="loading">
            <div class="loading-spinner"></div>
            <p>æ­£åœ¨åŠ è½½åšå®¢å†…å®¹...</p>
          </div>
          
          <!-- åˆ†é¡µæ§ä»¶ -->
            <div v-if="displayBlogs.length > 0 && !loading && totalPages > 1" class="pagination">
              <button class="pagination-btn" :disabled="currentPage === 0" @click="prevPage" :title="'ç¬¬ ' + (currentPage) + ' é¡µ'">
                <svg width="16" height="16" viewBox="0 0 24 24" fill="currentColor">
                  <path d="M15.41 7.41L14 6l-6 6 6 6 1.41-1.41L10.83 12z"/>
                </svg>
                ä¸Šä¸€é¡µ
              </button>
              
              <!-- é¡µç çœç•¥å‰ -->
              <span class="pagination-ellipsis" v-if="showEllipsisBefore">...</span>
              
              <!-- é¡µç æŒ‰é’® -->
              <button 
                v-for="page in visiblePages" 
                :key="page"
                class="pagination-btn page-number"
                :class="{ active: page === currentPage }"
                @click="changePage(page)"
                :title="'ç¬¬ ' + (page + 1) + ' é¡µ'"
              >
                {{ page + 1 }}
              </button>
              
              <!-- é¡µç çœç•¥å -->
              <span class="pagination-ellipsis" v-if="showEllipsisAfter">...</span>
              
              <button class="pagination-btn" :disabled="currentPage === totalPages - 1" @click="nextPage" :title="'ç¬¬ ' + (currentPage + 2) + ' é¡µ'">
                ä¸‹ä¸€é¡µ
                <svg width="16" height="16" viewBox="0 0 24 24" fill="currentColor">
                  <path d="M10 6L8.59 7.41 13.17 12l-4.58 4.59L10 18l6-6z"/>
                </svg>
              </button>
            </div>
        </div>
      </div>
      
      <!-- å³ä¾§ï¼šçƒ­é—¨è¯é¢˜å’Œåˆ†ç±» -->
      <div class="sidebar">
        <div class="sidebar-card">
          <HotTopics :limit="5" />
        </div>
        <div class="sidebar-card">
          <TagsSection title="çƒ­é—¨æ ‡ç­¾" :selectedTagId="selectedTagId" @tag-selected="handleTagSelect" />
        </div>
        <div class="sidebar-card">
          <CategorySection @select-category="handleCategorySelect" />
        </div>
        <div class="sidebar-card">
          <ReadHistory :limit="5" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getBlogList, searchBlogs } from '../api/blog';
import { getBlogsByTag } from '../api/tags';
import HotTopics from '../components/HotTopics.vue';
import CategorySection from '../components/CategorySection.vue';
import TagsSection from '../components/TagsSection.vue';
import ReadHistory from '../components/ReadHistory.vue';
import WelcomeAnimation from '../views/WelcomeAnimation.vue';

export default {
  name: 'Home',
  components: {
    HotTopics,
    CategorySection,
    TagsSection,
    ReadHistory,
    WelcomeAnimation
  },
  data() {
    return {
      blogs: [],
      searchKeyword: '',
      searchResults: [],
      isSearching: false,
      loading: false,
      selectedCategory: null,
      selectedTagId: null,
      followingAuthors: [],
      // æ’åºç›¸å…³æ•°æ®
      sortBy: 'latest', // é»˜è®¤æŒ‰æœ€æ–°æ’åº
      // åˆ†é¡µç›¸å…³æ•°æ®
      currentPage: 0,
      pageSize: 5,
      totalPages: 0,
      totalElements: 0
    }
  },
  created() {
    this.loadBlogs();
  },
  // ç›‘å¬è·¯ç”±å˜åŒ–ï¼Œå½“ç”¨æˆ·ä»è¯¦æƒ…é¡µè¿”å›æ—¶é‡æ–°åŠ è½½æ•°æ®
  watch: {
    '$route': {
      handler() {
        // å½“è·¯ç”±å˜åŒ–æ—¶é‡æ–°åŠ è½½åšå®¢åˆ—è¡¨ï¼Œç¡®ä¿æ•°æ®ä¸€è‡´æ€§
        this.loadBlogs();
      },
      immediate: false
    }
  },
  computed: {
    // æ ¹æ®æ˜¯å¦æœç´¢è¿”å›ä¸åŒçš„åšå®¢åˆ—è¡¨
    displayBlogs() {
      return this.isSearching ? this.searchResults : this.blogs;
    },
    // ç”Ÿæˆåˆ†é¡µé¡µç æ•°ç»„
    pageNumbers() {
      const pages = []
      const total = Math.min(this.totalPages, 5) // æœ€å¤šæ˜¾ç¤º5ä¸ªé¡µç 
      let start = Math.max(0, this.currentPage - 2)
      let end = Math.min(this.totalPages - 1, start + 4)
      
      if (end - start < 4) {
        start = Math.max(0, end - 4)
      }
      
      for (let i = start; i <= end; i++) {
        pages.push(i)
      }
      return pages
    },
    
    // è®¡ç®—å¯è§çš„é¡µç ï¼ˆç”¨äºå¤§é¡µæ•°æ—¶çš„çœç•¥æ˜¾ç¤ºï¼‰
    visiblePages() {
      const pages = [];
      const total = this.totalPages;
      const current = this.currentPage;
      
      // æ˜¾ç¤ºå‰3é¡µå’Œå3é¡µ
      const startPage = Math.max(0, current - 3);
      const endPage = Math.min(total - 1, current + 3);
      
      // å§‹ç»ˆæ˜¾ç¤ºç¬¬ä¸€é¡µ
      if (startPage > 0) {
        pages.push(0);
      }
      
      // æ·»åŠ ä¸­é—´çš„é¡µç 
      for (let i = startPage; i <= endPage; i++) {
        pages.push(i);
      }
      
      // å§‹ç»ˆæ˜¾ç¤ºæœ€åä¸€é¡µ
      if (endPage < total - 1) {
        pages.push(total - 1);
      }
      
      return pages;
    },
    
    // æ˜¯å¦æ˜¾ç¤ºå‰çœç•¥å·
    showEllipsisBefore() {
      return this.currentPage > 3;
    },
    
    // æ˜¯å¦æ˜¾ç¤ºåçœç•¥å·
    showEllipsisAfter() {
      return this.currentPage < this.totalPages - 4;
    }
  },
  methods: {
    // åŠ è½½åšå®¢åˆ—è¡¨
    async loadBlogs() {
      // é‡ç½®æœç´¢çŠ¶æ€ï¼Œç¡®ä¿åŠ è½½çš„æ˜¯åšå®¢åˆ—è¡¨è€Œä¸æ˜¯æœç´¢ç»“æœ
      this.isSearching = false;
      
      try {
        this.loading = true;
        // ä¼ é€’æ’åºå‚æ•°
        const response = await getBlogList(this.currentPage, this.pageSize, this.sortBy);
        // å¤„ç†åç«¯è¿”å›çš„åˆ†é¡µæ•°æ®
        if (response.data.content) {
          // åç«¯åˆ†é¡µæ•°æ®æ ¼å¼
          this.blogs = response.data.content;
          this.totalPages = response.data.totalPages;
          this.totalElements = response.data.totalElements;
        } else {
          // å…¼å®¹æ¨¡æ‹Ÿæ•°æ®
          this.blogs = response.data || [];
          this.totalPages = Math.ceil(this.blogs.length / this.pageSize);
          this.totalElements = this.blogs.length;
        }
      } catch (error) {
        console.error('è·å–åšå®¢åˆ—è¡¨å¤±è´¥:', error);
        this.blogs = [];
      } finally {
        this.loading = false;
      }
    },
    // å¤„ç†åˆ†ç±»é€‰æ‹©
    handleCategorySelect(category) {
      this.selectedCategory = category;
      console.log('é€‰æ‹©äº†åˆ†ç±»:', category.name);
      // è·³è½¬åˆ°åˆ†ç±»é¡µé¢
      this.$router.push(`/category/${category.id}`);
    },
    async handleSearch() {
      if (!this.searchKeyword.trim()) {
        this.isSearching = false;
        this.currentPage = 0;
        this.loadBlogs();
        return;
      }
      
      try {
        this.loading = true;
        // ä¼ é€’æ’åºå‚æ•°
        const response = await searchBlogs(this.searchKeyword.trim(), this.currentPage, this.pageSize, this.sortBy);
        // å¤„ç†åç«¯è¿”å›çš„åˆ†é¡µæ•°æ®
        if (response.data.content) {
          // åç«¯åˆ†é¡µæ•°æ®æ ¼å¼
          this.searchResults = response.data.content;
          this.totalPages = response.data.totalPages;
          this.totalElements = response.data.totalElements;
        } else {
          // å…¼å®¹æ¨¡æ‹Ÿæ•°æ®
          this.searchResults = response.data || [];
          this.totalPages = Math.ceil(this.searchResults.length / this.pageSize);
          this.totalElements = this.searchResults.length;
        }
        this.isSearching = true;
      } catch (error) {
        console.error('æœç´¢åšå®¢å¤±è´¥:', error);
        // å¤±è´¥æ—¶ä½¿ç”¨æœ¬åœ°è¿‡æ»¤ä½œä¸ºåå¤‡æ–¹æ¡ˆ
        this.localSearch();
      } finally {
        this.loading = false;
      }
    },
    // åˆ‡æ¢é¡µç 
    changePage(page) {
      if (page >= 0 && page < this.totalPages && page !== this.currentPage) {
        this.currentPage = page;
        // åˆ†é¡µåˆ‡æ¢æ—¶ä¸å†é‡æ–°åŠ è½½æ•°æ®ï¼Œç›´æ¥æ›´æ–°è§†å›¾
        // è¿™æ ·å¯ä»¥é¿å…åŠ è½½åŠ¨ç”»çš„è§¦å‘
      }
    },
    // ä¸Šä¸€é¡µ
    prevPage() {
      if (this.currentPage > 0) {
        this.changePage(this.currentPage - 1);
      }
    },
    // ä¸‹ä¸€é¡µ
    nextPage() {
      if (this.currentPage < this.totalPages - 1) {
        this.changePage(this.currentPage + 1);
      }
    },
    resetSearch() {
      this.searchKeyword = '';
      this.searchResults = [];
      this.isSearching = false;
    },
    
    // è®¾ç½®æ’åºæ–¹å¼
    setSortBy(sortType) {
      if (this.sortBy !== sortType) {
        this.sortBy = sortType;
        this.currentPage = 0; // åˆ‡æ¢æ’åºæ—¶é‡ç½®åˆ°ç¬¬ä¸€é¡µ
        if (this.isSearching) {
          this.handleSearch();
        } else {
          this.loadBlogs();
        }
      }
    },
    // æœ¬åœ°æœç´¢ä½œä¸ºåå¤‡æ–¹æ¡ˆ
    localSearch() {
      const keyword = this.searchKeyword.toLowerCase();
      this.searchResults = this.blogs.filter(blog => 
        blog.title.toLowerCase().includes(keyword) || 
        blog.content.toLowerCase().includes(keyword)
      );
      this.isSearching = true;
    },
    async handleTagSelect(tagData) {
      try {
        this.loading = true;
        this.selectedTagId = tagData.isActive ? tagData.id : null;
        this.currentPage = 0; // é‡ç½®é¡µç åˆ°ç¬¬ä¸€é¡µ
        
        if (tagData.isActive) {
            // ä¼ é€’æ’åºå‚æ•°
            const response = await getBlogsByTag(tagData.id, this.sortBy);
            
            // å¤„ç†åç«¯è¿”å›çš„æ•°æ®
            if (response.data.content) {
              // åç«¯åˆ†é¡µæ•°æ®æ ¼å¼
              this.searchResults = response.data.content;
              this.totalPages = response.data.totalPages;
              this.totalElements = response.data.totalElements;
            } else {
              // å…¼å®¹æ¨¡æ‹Ÿæ•°æ®ï¼Œè¿›è¡Œå‰ç«¯åˆ†é¡µ
              let allResults = response.data || [];
              
              // å‰ç«¯åˆ†é¡µ
              const start = this.currentPage * this.pageSize;
              const end = start + this.pageSize;
              
              this.searchResults = allResults.slice(start, end);
              this.totalPages = Math.ceil(allResults.length / this.pageSize);
              this.totalElements = allResults.length;
            }
            this.isSearching = true;
          } else {
          // å¦‚æœå–æ¶ˆé€‰æ‹©æ ‡ç­¾ï¼Œé‡æ–°åŠ è½½æ‰€æœ‰åšå®¢
          this.loadBlogs();
          this.isSearching = false;
        }
      } catch (error) {
        console.error('æŒ‰æ ‡ç­¾ç­›é€‰åšå®¢å¤±è´¥:', error);
      } finally {
        this.loading = false;
      }
    },
    viewDetail(id) {
      // è·³è½¬åˆ°åšå®¢è¯¦æƒ…é¡µ
      this.$router.push(`/blog/${id}`);
    },
    getSummary(content) {
      // æˆªå–å‰100ä¸ªå­—ç¬¦ä½œä¸ºæ‘˜è¦
      return content ? content.substring(0, 100) + '...' : '';
    },
    
    // æ·»åŠ æ•°å­—æ ¼å¼åŒ–æ–¹æ³•
    formatNumber(num) {
      if (num >= 10000) {
        return (num / 10000).toFixed(1) + 'w';
      } else if (num >= 1000) {
        return (num / 1000).toFixed(1) + 'k';
      }
      return num.toString();
    },
    
    isFollowing(author) {
      return this.followingAuthors.includes(author);
    },
    
    toggleFollow(author, event) {
        // æ·»åŠ ç‚¹å‡»åé¦ˆ - æ›´å¥å£®çš„å®ç°
        if (event && event.currentTarget) {
          const btn = event.currentTarget;
          btn.style.transform = 'scale(0.95)';
          setTimeout(() => {
            btn.style.transform = '';
          }, 200);
        }
        
        // çŠ¶æ€åˆ‡æ¢é€»è¾‘
        const newStatus = !this.isFollowing(author);
        if (newStatus) {
          // ä½¿ç”¨Vueçš„å“åº”å¼æ–¹æ³•æ·»åŠ å…ƒç´ 
          this.followingAuthors.push(author);
          console.log(`å·²æˆåŠŸå…³æ³¨${author}`);
        } else {
          // ä½¿ç”¨Vueçš„å“åº”å¼æ–¹æ³•ç§»é™¤å…ƒç´ 
          const index = this.followingAuthors.indexOf(author);
          if (index > -1) {
            this.followingAuthors.splice(index, 1);
          }
          console.log(`${author}å–æ¶ˆå…³æ³¨æˆåŠŸ`);
        }
        
        // æ³¨ï¼šåœ¨å®é™…é¡¹ç›®ä¸­ï¼Œè¿™é‡Œåº”è¯¥æ·»åŠ APIè°ƒç”¨æ¥æŒä¹…åŒ–å…³æ³¨çŠ¶æ€
        // å¯ä»¥ä½¿ç”¨try-catchå—å¤„ç†APIé”™è¯¯ï¼Œå¹¶åœ¨å¤±è´¥æ—¶å›æ»šçŠ¶æ€
      }
  }
}
</script>

<style scoped>
