.welcome-section {
  margin-top: 20px;
  margin-bottom: 30px;
}

/* è°ƒæ•´æ¬¢è¿åŠ¨ç”»ç»„ä»¶æ ·å¼ä»¥é€‚åº”é¦–é¡µ */
.welcome-section :deep(.welcome-container) {
  height: auto;
  min-height: 150px;
  background-color: transparent;
}

.welcome-section :deep(.animation-wrapper) {
  padding: 20px;
