  font-size: 1.8rem;
  font-weight: 600;
  color: var(--text-primary);
}

/* å“åº”å¼è°ƒæ•´ */
@media (max-width: 768px) {
  .welcome-section :deep(.animation-wrapper) {
    padding: 15px;
    margin: 0 10px;
  }
  .welcome-section :deep(.text-container h1) {
    font-size: 1.4rem;
  }
}

@media (max-width: 480px) {
  .welcome-section :deep(.text-container h1) {
    font-size: 1.2rem;
  }
