import axios from 'axios';

// ä½¿ç”¨æ¨¡æ‹Ÿæ•°æ®æ ‡å¿—
const USE_MOCK_DATA = true;

// åˆ›å»ºaxioså®ä¾‹
const api = axios.create({
  baseURL: '/api',
  timeout: 10000,
  headers: {
    'Content-Type': 'application/json'
  }
});

// æ¨¡æ‹Ÿé˜…è¯»å†å²æ•°æ®
const mockHistory = [
  { id: 1, blogId: 3, userId: 'user1', readAt: '2024-03-20T10:30:00', readProgress: 100 },
  { id: 2, blogId: 5, userId: 'user1', readAt: '2024-03-19T14:20:00', readProgress: 80 },
  { id: 3, blogId: 7, userId: 'user1', readAt: '2024-03-18T09:15:00', readProgress: 100 },
  { id: 4, blogId: 1, userId: 'user1', readAt: '2024-03-17T16:45:00', readProgress: 100 },
  { id: 5, blogId: 9, userId: 'user1', readAt: '2024-03-16T11:20:00', readProgress: 60 }
];

// æ¨¡æ‹Ÿç”¨æˆ·ID
const currentUserId = 'user1';

// å»¶è¿Ÿå‡½æ•°ï¼Œæ¨¡æ‹Ÿç½‘ç»œè¯·æ±‚
const delay = (ms) => new Promise(resolve => setTimeout(resolve, ms));

/**
 * è·å–ç”¨æˆ·çš„é˜…è¯»å†å²
 * @param {number} limit é™åˆ¶æ•°é‡
 * @returns {Promise} é˜…è¯»å†å²åˆ—è¡¨
 */
export const getUserReadHistory = async (limit = 20) => {
  if (USE_MOCK_DATA) {
    await delay(300);
    return { 
      data: [...mockHistory]
        .filter(history => history.userId === currentUserId)
        .sort((a, b) => new Date(b.readAt) - new Date(a.readAt))
        .slice(0, limit)
    };
  }
  return api.get(`/history?limit=${limit}`);
};

/**
 * æ·»åŠ é˜…è¯»å†å²
 * @param {number} blogId åšå®¢ID
 * @param {number} readProgress é˜…è¯»è¿›åº¦ï¼ˆç™¾åˆ†æ¯”ï¼‰
 * @returns {Promise} æ·»åŠ ç»“æœ
 */
export const addReadHistory = async (blogId, readProgress = 0) => {
  if (USE_MOCK_DATA) {
    await delay(200);
    // æ£€æŸ¥æ˜¯å¦å·²å­˜åœ¨è¯¥åšå®¢çš„é˜…è¯»è®°å½•
    const existingIndex = mockHistory.findIndex(
      history => history.userId === currentUserId && history.blogId === blogId
    );
    
    const now = new Date().toISOString();
    
    if (existingIndex !== -1) {
      // æ›´æ–°ç°æœ‰è®°å½•
      mockHistory[existingIndex].readAt = now;
      mockHistory[existingIndex].readProgress = Math.max(
        mockHistory[existingIndex].readProgress, 
        readProgress
      );
      return { data: mockHistory[existingIndex] };
    } else {
      // åˆ›å»ºæ–°è®°å½•
      const newId = Math.max(...mockHistory.map(h => h.id), 0) + 1;
      const newHistory = {
        id: newId,
        blogId,
        userId: currentUserId,
        readAt: now,
        readProgress
      };
      mockHistory.push(newHistory);
      return { data: newHistory };
    }
  }
  return api.post('/history', { blogId, readProgress });
};

/**
 * æ›´æ–°é˜…è¯»è¿›åº¦
 * @param {number} blogId åšå®¢ID
 * @param {number} readProgress é˜…è¯»è¿›åº¦ï¼ˆç™¾åˆ†æ¯”ï¼‰
 * @returns {Promise} æ›´æ–°ç»“æœ
 */
export const updateReadProgress = async (blogId, readProgress) => {
  if (USE_MOCK_DATA) {
    await delay(100);
    const history = mockHistory.find(
      h => h.userId === currentUserId && h.blogId === blogId
    );
    
    if (history) {
      history.readProgress = Math.max(history.readProgress, readProgress);
    }
    
    return { data: { success: true } };
  }
  return api.put(`/history/blog/${blogId}/progress`, { readProgress });
};

/**
 * æ¸…é™¤é˜…è¯»å†å²
 * @returns {Promise} æ¸…é™¤ç»“æœ
 */
export const clearReadHistory = async () => {
  if (USE_MOCK_DATA) {
    await delay(300);
    // è¿‡æ»¤æ‰å½“å‰ç”¨æˆ·çš„æ‰€æœ‰å†å²è®°å½•
    const index = mockHistory.findIndex(history => history.userId === currentUserId);
    while (index !== -1) {
      mockHistory.splice(index, 1);
      index = mockHistory.findIndex(history => history.userId === currentUserId);
    }
    return { data: { success: true } };
  }
  return api.delete('/history');
};
