<template>
  <div class="tags-section">
    <div class="section-header">
      <h3 class="section-title">{{ title }}</h3>
      
      <!-- æ ‡ç­¾æ’åºæ§åˆ¶ -->
      <div v-if="allowSort" class="sort-controls">
        <select v-model="sortBy" class="sort-select" @change="sortTags">
          <option value="count">æŒ‰çƒ­åº¦</option>
          <option value="name">æŒ‰åç§°</option>
          <option value="random">éšæœºæ’åº</option>
        </select>
      </div>
    </div>
    
    <!-- æ ‡ç­¾æœç´¢æ¡† -->
    <div v-if="allowSearch" class="search-container">
      <input 
        type="text" 
        v-model="searchKeyword" 
        class="search-input" 
        placeholder="æœç´¢æ ‡ç­¾..."
        @input="debounceSearch"
      >
    </div>
    
    <!-- æ ‡ç­¾äº‘ -->
    <div class="tags-cloud" v-if="displayedTags.length > 0">
      <transition-group name="tag" tag="span">
        <span 
          v-for="tag in displayedTags" 
          :key="tag.id" 
          class="tag-item"
          :class="{ active: selectedTagId === tag.id }"
          :style="getTagStyle(tag)"
          @click="selectTag(tag.id, tag.name)"
          @mouseenter="tagHover = tag.id"
          @mouseleave="tagHover = null"
        >
          <span class="tag-name">{{ tag.name }}</span>
          <span v-if="showCount" class="tag-count">({{ tag.count || 0 }})</span>
        </span>
      </transition-group>
    </div>
    
    <!-- ç©ºçŠ¶æ€ -->
    <div v-else-if="!loading" class="empty-tags">
      {{ emptyText || 'æš‚æ— æ ‡ç­¾' }}
    </div>
    
    <!-- åŠ è½½çŠ¶æ€ -->
    <div v-if="loading" class="loading-tags">
      <div class="loading-spinner"></div>
      <span>åŠ è½½æ ‡ç­¾ä¸­...</span>
    </div>
  </div>
</template>

<script>
import { getAllTags, getPopularTags } from '../api/tags';

export default {
  name: 'TagsSection',
  props: {
    title: {
      type: String,
      default: 'çƒ­é—¨æ ‡ç­¾'
    },
    showCount: {
      type: Boolean,
      default: true
    },
    isHotTags: {
      type: Boolean,
      default: true
    },
    limit: {
      type: Number,
      default: 20
    },
    emptyText: {
      type: String,
      default: ''
    },
    selectedTagId: {
      type: Number,
      default: null
    },
    // æ–°å¢å±æ€§
    allowSort: {
      type: Boolean,
      default: true
    },
    allowSearch: {
      type: Boolean,
      default: true
    },
    // åŠ¨æ€å¤§å°é…ç½®
    dynamicSize: {
      type: Boolean,
      default: true
    },
    // æœ€å°/æœ€å¤§å­—ä½“å¤§å°
    minFontSize: {
      type: Number,
      default: 12
    },
    maxFontSize: {
      type: Number,
      default: 18
    },
    // æ˜¯å¦ä½¿ç”¨æ ‡ç­¾é¢œè‰²
    useTagColors: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      tags: [],
      loading: false,
      // æ–°å¢çŠ¶æ€
      sortBy: 'count',
      searchKeyword: '',
      tagHover: null,
      searchTimer: null
    };
  },
  created() {
    this.fetchTags();
  },
  computed: {
    // è®¡ç®—è¦æ˜¾ç¤ºçš„æ ‡ç­¾ï¼ˆåº”ç”¨æœç´¢è¿‡æ»¤ï¼‰
    displayedTags() {
      let filtered = this.tags;
      
      // åº”ç”¨æœç´¢è¿‡æ»¤
      if (this.searchKeyword) {
        const keyword = this.searchKeyword.toLowerCase();
        filtered = filtered.filter(tag => 
          tag.name.toLowerCase().includes(keyword)
        );
      }
      
      return filtered;
    },
    // è®¡ç®—æ ‡ç­¾æ•°é‡çš„æœ€å¤§å€¼å’Œæœ€å°å€¼ï¼ˆç”¨äºåŠ¨æ€å¤§å°ï¼‰
    tagStats() {
      if (!this.tags.length) return { min: 0, max: 1 };
      
      const counts = this.tags.map(tag => tag.count || 0);
      return {
        min: Math.min(...counts),
        max: Math.max(...counts)
      };
    }
  },
  methods: {
    async fetchTags() {
      try {
        this.loading = true;
        let response;
        
        if (this.isHotTags) {
          response = await getPopularTags(this.limit);
        } else {
          response = await getAllTags();
        }
        
        this.tags = response.data || [];
        this.sortTags(); // åˆå§‹æ’åº
      } catch (error) {
        console.error('è·å–æ ‡ç­¾å¤±è´¥:', error);
        // å¯ä»¥æ·»åŠ é”™è¯¯æç¤ºç»™ç”¨æˆ·
        this.$emit('error', error);
      } finally {
        this.loading = false;
      }
    },
    selectTag(tagId, tagName) {
      // åˆ‡æ¢é€‰ä¸­çŠ¶æ€ (åŸºäºpropsä¸­çš„selectedTagId)
      const isActive = this.selectedTagId !== tagId;
      
      // å‘å‡ºäº‹ä»¶é€šçŸ¥çˆ¶ç»„ä»¶
      this.$emit('tag-selected', {
        id: tagId,
        name: tagName,
        isActive: isActive
      });
      
      // è®°å½•æ ‡ç­¾ç‚¹å‡»äº‹ä»¶ï¼ˆå¯ä»¥ç”¨äºç»Ÿè®¡ï¼‰
      this.$emit('tag-clicked', { id: tagId, name: tagName });
    },
    // æ’åºæ ‡ç­¾
    sortTags() {
      switch (this.sortBy) {
        case 'count':
          this.tags.sort((a, b) => (b.count || 0) - (a.count || 0));
          break;
        case 'name':
          this.tags.sort((a, b) => a.name.localeCompare(b.name));
          break;
        case 'random':
          // Fisher-Yates éšæœºæ’åºç®—æ³•
          for (let i = this.tags.length - 1; i > 0; i--) {
            const j = Math.floor(Math.random() * (i + 1));
            [this.tags[i], this.tags[j]] = [this.tags[j], this.tags[i]];
          }
          break;
      }
    },
    // é˜²æŠ–æœç´¢
    debounceSearch() {
      if (this.searchTimer) {
        clearTimeout(this.searchTimer);
      }
      
      this.searchTimer = setTimeout(() => {
        this.$emit('search', this.searchKeyword);
      }, 300);
    },
    // è·å–æ ‡ç­¾çš„æ ·å¼ï¼ˆåŠ¨æ€å¤§å°å’Œé¢œè‰²ï¼‰
    getTagStyle(tag) {
      const style = {};
      
      // åŠ¨æ€å­—ä½“å¤§å°
      if (this.dynamicSize) {
        const { min, max } = this.tagStats;
        const range = max - min || 1;
        const size = this.minFontSize + 
          ((tag.count || 0) - min) / range * (this.maxFontSize - this.minFontSize);
        style.fontSize = `${Math.round(size)}px`;
      }
      
      // ä½¿ç”¨æ ‡ç­¾é¢œè‰²
      if (this.useTagColors && tag.color) {
        // æ­£å¸¸çŠ¶æ€ä½¿ç”¨é¢œè‰²ä½œä¸ºæ–‡å­—é¢œè‰²
        style.color = tag.color;
        
        // é€‰ä¸­çŠ¶æ€çš„æ ·å¼é€šè¿‡CSSç±»æ§åˆ¶
        // æ‚¬åœæ•ˆæœé€šè¿‡CSSä¼ªç±»æ§åˆ¶
      }
      
      // æ‚¬åœæ—¶çš„ç‰¹æ®Šæ•ˆæœ
      if (this.tagHover === tag.id) {
        style.transform = 'scale(1.05) translateY(-2px)';
      }
      
      return style;
    },
    // æ‰‹åŠ¨åˆ·æ–°æ ‡ç­¾
    refreshTags() {
      this.fetchTags();
    }
  },
  beforeDestroy() {
    if (this.searchTimer) {
      clearTimeout(this.searchTimer);
    }
  }
};
</script>

