<template>
  <div class="read-history">
    <div class="section-header">
      <h3 class="section-title">é˜…è¯»å†å²</h3>
      <button 
        v-if="history.length > 0" 
        class="clear-btn"
        @click="confirmClearHistory"
        :disabled="clearing"
      >
        {{ clearing ? 'æ¸…é™¤ä¸­...' : 'æ¸…ç©º' }}
      </button>
    </div>
    
    <!-- å†å²è®°å½•åˆ—è¡¨ -->
    <div class="history-list" v-if="history.length > 0">
      <div v-for="item in history" :key="item.id" class="history-item">
        <div class="history-content" @click="viewBlog(item.blogId)">
          <h4 class="blog-title">{{ getBlogTitle(item.blogId) }}</h4>
          <div class="history-meta">
            <span class="read-time">{{ formatTime(item.readAt) }}</span>
            <span class="read-progress" v-if="item.readProgress > 0">
              {{ item.readProgress }}% å·²è¯»
            </span>
          </div>
        </div>
      </div>
    </div>
    
    <!-- ç©ºçŠ¶æ€ -->
    <div v-else-if="!loading" class="empty-history">
      æš‚æ— é˜…è¯»è®°å½•
    </div>
    
    <!-- åŠ è½½çŠ¶æ€ -->
    <div v-if="loading" class="loading-history">
      åŠ è½½é˜…è¯»è®°å½•ä¸­...
    </div>
  </div>
</template>

<script>
import { getUserReadHistory, clearReadHistory } from '../api/history';
import { getBlogList } from '../api/blog';

export default {
  name: 'ReadHistory',
  props: {
    limit: {
      type: Number,
      default: 10
    }
  },
  data() {
    return {
      history: [],
      loading: false,
      clearing: false,
      blogList: []
    };
  },
  created() {
    this.fetchBlogList();
    this.fetchReadHistory();
  },
  // ç›‘å¬è·¯ç”±å˜åŒ–ï¼Œå½“ç»„ä»¶åœ¨é¡µé¢ä¸­ä¸”è·¯ç”±å˜åŒ–æ—¶åˆ·æ–°å†å²è®°å½•
  watch: {
    $route: {
      handler() {
        this.fetchReadHistory();
      },
      deep: true
    }
  },
  methods: {
    async fetchReadHistory() {
      try {
        this.loading = true;
        const response = await getUserReadHistory(this.limit);
        this.history = response.data || [];
      } catch (error) {
        console.error('è·å–é˜…è¯»å†å²å¤±è´¥:', error);
      } finally {
        this.loading = false;
      }
    },
    async fetchBlogList() {
      try {
        const response = await getBlogList();
        // é€‚é…æ–°çš„åˆ†é¡µæ•°æ®æ ¼å¼ï¼Œä»contentå­—æ®µè·å–åšå®¢æ•°ç»„
        this.blogList = response.data.content || [];
      } catch (error) {
        console.error('è·å–åšå®¢åˆ—è¡¨å¤±è´¥:', error);
      }
    },
    getBlogTitle(blogId) {
      // ä»è·å–çš„åšå®¢åˆ—è¡¨ä¸­æŸ¥æ‰¾æ ‡é¢˜
      const blog = this.blogList.find(b => b.id === blogId);
      return blog ? blog.title : `åšå®¢ #${blogId}`;
    },
    viewBlog(blogId) {
      this.$router.push(`/blog/${blogId}`);
    },
    confirmClearHistory() {
      if (window.confirm('ç¡®å®šè¦æ¸…ç©ºæ‰€æœ‰é˜…è¯»å†å²è®°å½•å—ï¼Ÿæ­¤æ“ä½œä¸å¯æ¢å¤ã€‚')) {
        this.clearAllHistory();
      }
    },
    async clearAllHistory() {
      try {
        this.clearing = true;
        await clearReadHistory();
        this.history = [];
      } catch (error) {
        console.error('æ¸…ç©ºé˜…è¯»å†å²å¤±è´¥:', error);
        // å¯ä»¥åœ¨è¿™é‡Œæ·»åŠ ç”¨æˆ·å‹å¥½çš„é”™è¯¯æç¤º
      } finally {
        this.clearing = false;
      }
    },
    formatTime(timeStr) {
      if (!timeStr) return '';
      
      const date = new Date(timeStr);
      const now = new Date();
      const diff = now - date;
      
      // å°äº1åˆ†é’Ÿ
      if (diff < 60 * 1000) {
        return 'åˆšåˆšé˜…è¯»';
      }
      // å°äº1å°æ—¶
      if (diff < 60 * 60 * 1000) {
        return `${Math.floor(diff / (60 * 1000))}åˆ†é’Ÿå‰é˜…è¯»`;
      }
      // å°äº24å°æ—¶
      if (diff < 24 * 60 * 60 * 1000) {
        return `${Math.floor(diff / (60 * 60 * 1000))}å°æ—¶å‰é˜…è¯»`;
      }
      // å°äº30å¤©
      if (diff < 30 * 24 * 60 * 60 * 1000) {
        return `${Math.floor(diff / (24 * 60 * 60 * 1000))}å¤©å‰é˜…è¯»`;
      }
      
      // æ ¼å¼åŒ–æ—¥æœŸ
      return `${date.getFullYear()}-${String(date.getMonth() + 1).padStart(2, '0')}-${String(date.getDate()).padStart(2, '0')} é˜…è¯»`;
    }
  }
};
</script>

