<template>
  <div class="hot-topics">
    <h3>çƒ­é—¨è¯é¢˜</h3>
    <div v-if="loading" class="loading">åŠ è½½ä¸­...</div>
    <div v-else-if="hotTopics.length === 0" class="empty">æš‚æ— çƒ­é—¨è¯é¢˜</div>
    <div v-else class="topics-list">
      <div 
        v-for="(topic, index) in hotTopics" 
        :key="topic.id" 
        class="topic-item"
        @click="navigateToTopic(topic)"
        :title="topic.title"
      >
        <span class="topic-rank" :class="{ top3: index < 3 }">{{ index + 1 }}</span>
        <div class="topic-content">
          <h4 class="topic-title">{{ topic.title }}</h4>
          <div class="topic-meta">
            <span class="topic-category">{{ topic.category }}</span>
            <span class="topic-stats">
              <span class="views">{{ formatNumber(topic.views) }} æµè§ˆ</span>
              <span class="comments">{{ formatNumber(topic.comments) }} è¯„è®º</span>
            </span>
          </div>
        </div>
      </div>
    </div>
    <div class="refresh-container" v-if="hotTopics.length > 0">
      <button class="refresh-btn" @click="refreshTopics" :disabled="loading">
        åˆ·æ–°è¯é¢˜
      </button>
    </div>
  </div>
</template>

<script>
import { getHotTopics } from '../api/topics';

export default {
  name: 'HotTopics',
  props: {
    limit: {
      type: Number,
      default: 5
    }
  },
  data() {
    return {
      hotTopics: [],
      loading: false
    };
  },
  created() {
    this.fetchHotTopics();
    // æ¯5åˆ†é’Ÿè‡ªåŠ¨åˆ·æ–°ä¸€æ¬¡æ•°æ®
    this.refreshTimer = setInterval(() => {
      this.fetchHotTopics();
    }, 5 * 60 * 1000);
  },
  beforeDestroy() {
    // æ¸…ç†å®šæ—¶å™¨
    if (this.refreshTimer) {
      clearInterval(this.refreshTimer);
    }
  },
  methods: {
    async fetchHotTopics() {
      try {
        this.loading = true;
        const response = await getHotTopics(this.limit);
        this.hotTopics = response.data || [];
      } catch (error) {
        console.error('è·å–çƒ­é—¨è¯é¢˜å¤±è´¥:', error);
        // ç®€å•çš„é”™è¯¯æç¤º
        alert('è·å–çƒ­é—¨è¯é¢˜å¤±è´¥ï¼Œè¯·ç¨åé‡è¯•');
      } finally {
        this.loading = false;
      }
    },
    
    // æ‰‹åŠ¨åˆ·æ–°è¯é¢˜
    refreshTopics() {
      this.fetchHotTopics();
    },
    
    // è·³è½¬åˆ°è¯é¢˜è¯¦æƒ…é¡µ
    navigateToTopic(topic) {
      // å¢åŠ ç‚¹å‡»è®¡æ•°ï¼ˆå®é™…åº”ç”¨ä¸­å¯ä»¥å‘é€è¯·æ±‚åˆ°åç«¯ï¼‰
      console.log('æŸ¥çœ‹è¯é¢˜:', topic.title, 'ID:', topic.id);
      
      // è·³è½¬åˆ°è¯é¢˜è¯¦æƒ…é¡µ
      this.$router.push({
        path: `/topic/${topic.id}`,
        query: { title: topic.title, category: topic.category }
      });
    },
    
    // æ ¼å¼åŒ–æ•°å­—ï¼ˆå¦‚ï¼š1234 -> 1.2kï¼‰
    formatNumber(num) {
      if (num >= 10000) {
        return (num / 10000).toFixed(1) + 'w';
      } else if (num >= 1000) {
        return (num / 1000).toFixed(1) + 'k';
      }
      return num.toString();
    }
  }
};
</script>

