<script>
import { getCategories } from '../api/topics';

export default {
  name: 'CategorySection',
  data() {
    return {
      // åˆ†ç±»æ•°æ®ï¼Œä¸CategoryPageä¿æŒä¸€è‡´
      categories: [
        { id: 1, name: 'æŠ€æœ¯åˆ†äº«', description: 'åˆ†äº«å„ç±»æŠ€æœ¯çŸ¥è¯†å’Œç»éªŒ', icon: 'ğŸ’»', color: '#4a90e2', postCount: 42, activeUsers: 128, activity: 75 },
        { id: 2, name: 'ç”Ÿæ´»éšç¬”', description: 'è®°å½•ç”Ÿæ´»ä¸­çš„ç‚¹ç‚¹æ»´æ»´', icon: 'ğŸ“', color: '#52c41a', postCount: 28, activeUsers: 89, activity: 60 },
        { id: 3, name: 'èŒåœºç»éªŒ', description: 'èŒåœºå¿ƒå¾—ä¸èŒä¸šå‘å±•å»ºè®®', icon: 'ğŸ’¼', color: '#faad14', postCount: 36, activeUsers: 156, activity: 82 },
        { id: 4, name: 'å…´è¶£çˆ±å¥½', description: 'ä¸ªäººå…´è¶£ä¸çˆ±å¥½äº¤æµ', icon: 'ğŸ®', color: '#f5222d', postCount: 51, activeUsers: 203, activity: 90 },
        { id: 5, name: 'å­¦ä¹ èµ„æº', description: 'ä¼˜è´¨å­¦ä¹ èµ„æ–™ä¸æ•™ç¨‹åˆ†äº«', icon: 'ğŸ“š', color: '#1890ff', postCount: 33, activeUsers: 178, activity: 85 },
        { id: 6, name: 'å¥åº·ç”Ÿæ´»', description: 'å¥åº·é¥®é£Ÿä¸ç”Ÿæ´»æ–¹å¼', icon: 'ğŸƒ', color: '#52c41a', postCount: 24, activeUsers: 92, activity: 55 },
        { id: 7, name: 'æ—…è¡Œè§é—»', description: 'æ—…è¡Œç»å†ä¸æ™¯ç‚¹æ¨è', icon: 'âœˆï¸', color: '#fa8c16', postCount: 47, activeUsers: 145, activity: 78 },
        { id: 8, name: 'ç§‘æŠ€å‰æ²¿', description: 'æœ€æ–°ç§‘æŠ€åŠ¨æ€ä¸è¶‹åŠ¿', icon: 'ğŸš€', color: '#722ed1', postCount: 38, activeUsers: 167, activity: 88 }
      ],
      loading: false,
      sortBy: 'name',
      expandedSubcategories: []
    };
  },
  created() {
    this.fetchCategories();
  },
  computed: {
    // æ’åºåçš„åˆ†ç±»åˆ—è¡¨
    sortedCategories() {
      const categories = [...this.categories];
      
      switch (this.sortBy) {
        case 'count':
          return categories.sort((a, b) => (b.count || 0) - (a.count || 0));
        case 'activity':
          return categories.sort((a, b) => (b.activity || 0) - (a.activity || 0));
        case 'name':
        default:
          return categories.sort((a, b) => a.name.localeCompare(b.name));
      }
    }
  },
  methods: {
    async fetchCategories() {
      try {
        this.loading = true;
        const response = await getCategories();
        let categories = response.data || [];
        
        // ä¸ºæ¼”ç¤ºç›®çš„ï¼Œæ·»åŠ ä¸€äº›æ¨¡æ‹Ÿæ•°æ®
        this.categories = this.enrichCategories(categories);
      } catch (error) {
        console.error('è·å–åˆ†ç±»å¤±è´¥:', error);
        // é”™è¯¯æ—¶ä½¿ç”¨æ¨¡æ‹Ÿæ•°æ®
        this.categories = this.getMockCategories();
      } finally {
        this.loading = false;
      }
    },
    
    // ä¸°å¯Œåˆ†ç±»æ•°æ®ï¼Œæ·»åŠ æ¼”ç¤ºç”¨çš„é¢å¤–ä¿¡æ¯
    enrichCategories(categories) {
      const icons = ['ğŸ“', 'ğŸ’»', 'ğŸ¨', 'ğŸ“Š', 'ğŸš€', 'ğŸ®', 'ğŸ“±', 'ğŸµ', 'ğŸ“š', 'ğŸ¯'];
      const descriptions = [
        'æœ€æ–°æŠ€æœ¯åŠ¨æ€å’Œè¶‹åŠ¿åˆ†æ',
        'å¼€å‘ç»éªŒåˆ†äº«å’Œæ•™ç¨‹',
        'è®¾è®¡ç†å¿µå’Œåˆ›æ„ä½œå“',
        'æ•°æ®åˆ†æå’Œå¯è§†åŒ–',
        'é¡¹ç›®ç®¡ç†å’Œæ•ˆç‡å·¥å…·',
        'æ¸¸æˆå¼€å‘å’Œè¯„æµ‹',
        'ç§»åŠ¨åº”ç”¨å¼€å‘å’Œä½“éªŒ',
        'éŸ³ä¹åˆ¶ä½œå’Œæ¬£èµ',
        'è¯»ä¹¦ç¬”è®°å’Œæ¨è',
        'ç›®æ ‡è®¾å®šå’Œè‡ªæˆ‘æå‡'
      ];
      
      return categories.map((category, index) => ({
        ...category,
        icon: category.icon || icons[index % icons.length],
        description: category.description || descriptions[index % descriptions.length],
        count: category.count || Math.floor(Math.random() * 50) + 5,
        activity: category.activity || Math.floor(Math.random() * 100),
        isHot: category.isHot || (index % 3 === 0),
        updatedAt: category.updatedAt || new Date(Date.now() - Math.random() * 7 * 24 * 60 * 60 * 1000).toISOString(),
        subcategories: category.subcategories || this.generateSubcategories(category.name, index)
      }));
    },
    
    // ç”Ÿæˆæ¨¡æ‹Ÿå­åˆ†ç±»
    generateSubcategories(parentName, index) {
      const subcategories = [
        [`${parentName}-åŸºç¡€`, `${parentName}-è¿›é˜¶`, `${parentName}-é«˜çº§`],
        [`${parentName}-æ•™ç¨‹`, `${parentName}-æ¡ˆä¾‹`, `${parentName}-å·¥å…·`],
        [`${parentName}-å‰ç«¯`, `${parentName}-åç«¯`, `${parentName}-æ¶æ„`]
      ];
      
      // éšæœºå†³å®šæ˜¯å¦æœ‰å­åˆ†ç±»ï¼Œä»¥åŠæœ‰å¤šå°‘ä¸ªå­åˆ†ç±»
      if (Math.random() > 0.3) {
        const template = subcategories[index % subcategories.length];
        const count = Math.floor(Math.random() * 2) + 1; // 1-3ä¸ªå­åˆ†ç±»
        return template.slice(0, count).map((name, i) => ({
          id: `${index}-${i}`,
          name,
          count: Math.floor(Math.random() * 20) + 1
        }));
      }
      return [];
    },
    
    // è·å–å®Œå…¨æ¨¡æ‹Ÿçš„åˆ†ç±»æ•°æ®ï¼ˆå½“APIè°ƒç”¨å¤±è´¥æ—¶ï¼‰
    getMockCategories() {
      const mockNames = ['æŠ€æœ¯åšå®¢', 'è®¾è®¡åˆ†äº«', 'äº§å“æ€ç»´', 'å¼€å‘æ•™ç¨‹', 'æ•°æ®åˆ†æ', 'é¡¹ç›®ç®¡ç†'];
      const colors = ['#42b983', '#3498db', '#e74c3c', '#f39c12', '#9b59b6', '#1abc9c'];
      
      return mockNames.map((name, index) => ({
        id: index + 1,
        name,
        color: colors[index % colors.length],
        icon: ['ğŸ“', 'ğŸ’»', 'ğŸ¨', 'ğŸ“Š', 'ğŸš€', 'ğŸ¯'][index % 6],
        description: `è¿™æ˜¯å…³äº${name}çš„åˆ†ç±»æè¿°ï¼ŒåŒ…å«äº†ä¸°å¯Œçš„ç›¸å…³å†…å®¹ã€‚`,
        count: Math.floor(Math.random() * 50) + 10,
        activity: Math.floor(Math.random() * 100),
        isHot: index % 3 === 0,
        updatedAt: new Date(Date.now() - Math.random() * 7 * 24 * 60 * 60 * 1000).toISOString(),
        subcategories: this.generateSubcategories(name, index)
      }));
    },
    
    // æ’åºåˆ†ç±»
    sortCategories() {
      // æ’åºé€»è¾‘å·²åœ¨computedä¸­å¤„ç†
    },
    
    // åˆ‡æ¢å­åˆ†ç±»æ˜¾ç¤º
    toggleSubcategories(categoryId) {
      const index = this.expandedSubcategories.indexOf(categoryId);
      if (index > -1) {
        this.expandedSubcategories.splice(index, 1);
      } else {
        this.expandedSubcategories.push(categoryId);
      }
    },
    
    // æ ¼å¼åŒ–æ›´æ–°æ—¶é—´
    formatUpdateTime(dateString) {
      if (!dateString) return 'æœªçŸ¥æ›´æ–°';
      
      const date = new Date(dateString);
      const now = new Date();
      const diffDays = Math.floor((now - date) / (1000 * 60 * 60 * 24));
      
      if (diffDays === 0) return 'ä»Šå¤©æ›´æ–°';
      if (diffDays === 1) return 'æ˜¨å¤©æ›´æ–°';
      if (diffDays < 7) return `${diffDays}å¤©å‰æ›´æ–°`;
      if (diffDays < 30) return `${Math.floor(diffDays / 7)}å‘¨å‰æ›´æ–°`;
      return date.toLocaleDateString('zh-CN');
    },
    
    // æŸ¥çœ‹å…¨éƒ¨åˆ†ç±»
    viewAllCategories() {
      this.$router.push('/categories');
    }
  }
};
</script>

